.popup{
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba($color: #000000, $alpha: .8);
    z-index: 9999;

    opacity: 0;
    visibility: hidden;
    transition: all .3;

    &:target{
        opacity: 1;
        visibility: visible;
    }

    &:target &__content{
        opacity: 1;
        transform: translate(-50%,-50% ) scale(1);
    }

    &__close{
        &:link,
        &:visited{
            color: $color-grey-dark;
            position: absolute;
            top: 2.5rem;
            right: 2.5rem;
            font-size: 3rem;
            text-decoration: none;
            display: inline-block;
            transition: all .2s;
        }

        &:hover{
            color: $color-primary;
        }

    }


    &__content{
        @include absCenter;

        width: 75%;
        background-color: white;
        box-shadow: 0 2rem 4rem rgba($color: #000000, $alpha: 0.2);
        display: table;
        overflow: hidden;

        opacity: 0;
        transform: translate(-50%,-50% ) scale(.25);
        transition: all .5s .2s;
    }

    &__left{
        width: 33.333%;
        display: table-cell;
    }
    
    &__right{
        width: 66.666%;
        display: table-cell;
        vertical-align: middle;
        padding: 3rem 5rem;
    }

    &__img{
        display: block;
        width: 100%;
    }

    &__text{
        font-size: 1.4rem;
        margin-bottom: 4rem;

        column-count: 2;
        column-gap: 4rem;
        column-rule: 1px solid $color-grey-light-1;

        -moz-hyphens: auto;
        -ms-hyphens: auto;
        -webkit-hyphens: auto;
        hyphens: auto;

        text-align: justify;
    }

}