.section-about {
    background-color: $color-grey-light-1;
    padding: 25rem 0 20rem 0;
    margin-top: -20vh;
    
}

.section-features{
    padding: 20rem 0;
    background-image: 
    linear-gradient(to right bottom,rgba(126,213,111,0.8), rgba(40,180,131,0.8)),
    url("../img/nat-4.jpg");
    background-size: cover;

    transform: skewY(-7deg);
    margin-top: -11rem;

    & > *{
        transform: skewY(7deg);
    }
}

.section-tours{
    background-color: $color-grey-light-1;
    padding: 25rem 0 15rem 0;
    margin-top: -11rem;
}

.section-stories{
    padding: 15rem 0;
    position: relative;
}

.section-book{
    padding: 15rem 0;
    background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);
}

.book{
    background-image: linear-gradient(105deg,
                                      rgba(white, .9) 0%,
                                      rgba(white, .9) 50%,
                                      transparent 50%),
                                      url(../img/nat-10.jpg);
    background-size: cover;
    border-radius: 3px;
    box-shadow: 0 1.5rem 4rem rgba(black, .2);
    height: 50rem;

    &__form{
        width: 50%;
        padding: 6rem;
    }

}